*{
    margin: 0;
    padding: 0;
}

.app{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.board {
    width: calc(64px * 8);
    height: calc(64px * 8);
    display: flex;
    flex-wrap: wrap;
}

.cell{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cell img{
    height: 48px;
    width: 48px;
    position: relative;
}

.black{
    background: #100f0f;
}

.white{
    background: #cccccc;
}

.selected{
    background: brown;
}

.available{
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background-color: green;
}